<template>
    <Header/>
    <router-view/>
</template>

<script>
import Header from '@/components/Header.vue';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

export default {
    name: 'App',
    components: {
        Header
    },
    mounted: function() {

        window.addEventListener('load', () => {
            let introTl = gsap.timeline();

            introTl.fromTo('.landing-page--in img', { scale: 1.6, filter: 'blur(3px)', duration: 2, ease: "power3.inOut" }, { scale: 1.1, filter: 'blur(0px)', duration: 2, ease: "power3.inOut" });
            introTl.fromTo('.landing-page--middle img', { scale: 1.6, filter: 'blur(3px)', duration: 2, ease: "power3.inOut" }, { scale: 1.1, filter: 'blur(0px)', duration: 2, ease: "power3.inOut" }, "<0.1");
            introTl.fromTo('.landing-page--out img', { scale: 1.6, filter: 'blur(3px)', duration: 2, ease: "power3.inOut" }, { scale: 1.1, filter: 'blur(0px)', duration: 2, ease: "power3.inOut" }, "<0.2");

            ScrollTrigger.create({
                toggleClass: { targets: 'header', className: 'colorChange' },
                trigger: '.introduce',
                start: 'top top',
                endTrigger: '#home',
                end: 'bottom+=100 bottom',
                // markers: true,
            })
        })
    }
}
</script>

