<template>
    <div id="viewport">
        <div class="home" id="home">
            <div class="landing-page--container" @click="goDetail">
                <div :class="landClass[data-1]" class="landing-page--default" v-for="data in 3" :key="data">
                    <img alt="Piano Room" src="@/assets/img/main/piano.jpg">
                    <div class="landing-page--cover" v-if="data == 2"></div>
                </div>
    
                <div class="landing-page--title-container">
                    <div class="landing-page--title"><span>우리들의음악공간</span></div>
                    <div class="landing-page--line"></div>
                    <div class="landing-page--subtitle">our music space</div>
                </div>
            </div>
    
            <Intro/>
            <!-- <div class="introduce">
                                              <div class="greeting">
                                                  <h1 class="title">안녕하세요, 피아노 클래스 입니다.</h1>
                                              </div>
                                            </div> -->
        </div>
    </div>
</template>

<script>
import Intro from '@/components/Introduce.vue';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

export default {
    name: 'Home',
    components: {
        Intro
    },
    data: function() {
        return {
            landClass: ['landing-page--out', 'landing-page--middle', 'landing-page--in'],
        }
    },
    methods: {
        goDetail: function() {
            let docHeight = window.innerHeight;
            for (let i = 0; i < docHeight + 10; i++) {
                setTimeout(() => {
                    window.scrollTo(0, i);
                }, 0.5 * i);
            }
        }
    },
    mounted: function() {
        smoothScroll('#home');

        ScrollTrigger.create({
            animation: gsap.to('.landing-page--container', {
                yPercent: -30
            }),
            trigger: '.landing-page--container',
            start: 'top top',
            end: 'bottom top',
            scrub: true,
            pin: true
        })

        ScrollTrigger.create({
            trigger: '.introduce-bg',
            start: 'top top',
            endTrigger: '.home',
            end: 'bottom bottom',
            pin: true
        })

        ScrollTrigger.create({
            animation: gsap.to('.introduce-bg img', {
                y: 2881 - document.querySelector('.introduce').clientHeight
            }),
            trigger: '.introduce',
            start: 'top top',
            endTrigger: '#home',
            end: 'bottom bottom',
            scrub: true,
        })

        let tl = gsap.timeline({
            scrollTrigger: {
                trigger: '.introduce',
                start: 'top top',
                endTrigger: '#home',
                end: 'center bottom',
                scrub: true,
                markers: true,
            }
        })

        tl.to('.section--1 .img-area:nth-of-type(3)', {
            y: -200,
            ease: 'none'
        }, '<')

        tl.to('.section--1 .img-area:nth-of-type(1)', {
            y: -100,
            ease: 'none'
        }, '<')

        tl.to('.section--1 .img-area:nth-of-type(2)', {
            y: -500,
            ease: 'none'
        }, '<')

        function smoothScroll(content, viewport, smoothness) {
            content = gsap.utils.toArray(content)[0];
            smoothness = smoothness || 1;

            gsap.set(viewport || content.parentNode, { overflow: "hidden", position: "fixed", height: "100%", width: "100%", top: 0, left: 0, right: 0, bottom: 0 });
            gsap.set(content, { overflow: "visible", width: "100%" });

            let getProp = gsap.getProperty(content),
                setProp = gsap.quickSetter(content, "y", "px"),
                setScroll = ScrollTrigger.getScrollFunc(window),
                removeScroll = () => content.style.overflow = "visible",
                killScrub = trigger => {
                    let scrub = trigger.getTween ? trigger.getTween() : gsap.getTweensOf(trigger.animation)[0]; // getTween() was added in 3.6.2
                    scrub && scrub.kill();
                    trigger.animation.progress(trigger.progress);
                },
                height, isProxyScrolling;

            function refreshHeight() {
                height = content.clientHeight;
                content.style.overflow = "visible"
                document.body.style.height = height + "px";
                return height - document.documentElement.clientHeight;
            }

            ScrollTrigger.addEventListener("refresh", () => {
                removeScroll();
                requestAnimationFrame(removeScroll);
            })
            ScrollTrigger.defaults({ scroller: content });
            ScrollTrigger.prototype.update = p => p; // works around an issue in ScrollTrigger 3.6.1 and earlier (fixed in 3.6.2, so this line could be deleted if you're using 3.6.2 or later)

            ScrollTrigger.scrollerProxy(content, {
                scrollTop(value) {
                    if (arguments.length) {
                        isProxyScrolling = true; // otherwise, if snapping was applied (or anything that attempted to SET the scroll proxy's scroll position), we'd set the scroll here which would then (on the next tick) update the content tween/ScrollTrigger which would try to smoothly animate to that new value, thus the scrub tween would impede the progress. So we use this flag to respond accordingly in the ScrollTrigger's onUpdate and effectively force the scrub to its end immediately.
                        setProp(-value);
                        setScroll(value);
                        return;
                    }
                    return -getProp("y");
                },
                scrollHeight: () => document.body.scrollHeight,
                getBoundingClientRect() {
                    return { top: 0, left: 0, width: window.innerWidth, height: window.innerHeight };
                }
            });

            return ScrollTrigger.create({
                animation: gsap.fromTo(content, { y: 0 }, {
                    y: () => document.documentElement.clientHeight - height,
                    ease: "none",
                    onUpdate: ScrollTrigger.update
                }),
                scroller: window,
                invalidateOnRefresh: true,
                start: 0,
                end: refreshHeight,
                refreshPriority: -999,
                scrub: smoothness,
                onUpdate: self => {
                    if (isProxyScrolling) {
                        killScrub(self);
                        isProxyScrolling = false;
                    }
                },
                onRefresh: killScrub // when the screen resizes, we just want the animation to immediately go to the appropriate spot rather than animating there, so basically kill the scrub.
            });
        }
    }
}
</script>

